body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header{
  background-color: black;
  color:white;
  padding: 5px;
  text-align: center;
  font-size: 10px;
  font-weight: bold;
  letter-spacing: 2px;
}
.score{
  background-color: yellow;
  width: 40px;
  font-size: 13px;
  font-weight: bold;
  float: right;
  text-align: center;
  margin: 10px ;
  padding: 4px;
  font-size: 8px;
}

.rev{
  background-color: rgb(56, 210, 56);
  width: 20px;
  text-align: center;
  position: relative;
  top:70px;
  left:50%;
  border: 0.4px solid black;
  border-radius: 50%;
  font-size: 10px;
  padding: 4px;
}
.para{
  font-size: 10px;
  position: relative;
  top:70px;
  left:43%;
}
input{
  font-size: 5px;
  width:20px;
  height: 7px;
  position: relative;
  top:60px;
  left: 50%;
  border: 1px solid black;
}
.btn{
  font-size: 5px;
  position: relative;
  top:50px;
  left:50%;
  border: 1px solid black;
}
.suggest{
  width:50px;
  padding: 4px;
  background-color: green;
  font-size: 6px;
  font-weight: bold;
  text-align: center;
  position: relative;
  top:53px;
  left: 48%;
}


